import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, CreditoCiap } from '../../../../models/dtos/mlf/creditoCiap';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface CreditoCiapEditViewProps extends EditViewProps<CreditoCiap> {}

const CreditoCiapEditView: React.FC<CreditoCiapEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<any>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  const setIdEmpresa = () => {
    const {
      empresa: { id },
    } = globalReducer.globalParameter;

    setTimeout(() => {
      formProps.setValue('empresa.id', id);
    }, 500);
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        setIdEmpresa();
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Saldo Anterior do CIAP">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="empresa.id" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Referência"
              type={InputType.REFERENCE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="vrCredito"
              label="Valor de Crédito"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o Valor de Crédito"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CreditoCiapEditView;
